.work-services {
  margin-top: -58px;
  // padding-top: 212px;
  // padding-bottom: 80px;

  &__list {
    margin-bottom: 78px;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    gap: 17px;
  }
}

.work-services-list {
  &__item {
    grid-column: 2 span;
  }
}

.work-services-item {
  position: relative;
  display: block;
  padding: 26px 10px;

  border: 1px solid #e5e8ed;
  border-radius: 4px;

  box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 4px 4px -4px rgba(30, 33, 44, 0.03);

  background: var(--color-light);
  transition: 0.3s ease-in-out;

  // overlay gray
  &::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 4;

    border-radius: inherit;

    background-color: var(--color-primary);

    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  &__content {
    @include flex-collumn-centr;

    position: relative;
    z-index: 10;
  }

  &__icon {
    fill: var(--color-primary);

    width: 52px;
    height: 52px;
    object-fit: cover;

    margin-bottom: 18px;

    transition: fill 0.3s ease-in-out;
  }

  &__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    text-align: center;

    color: var(--color-ebony);

    transition: color 0.3s ease-in-out;
  }

  &:hover {
    box-shadow: 0px 60px 80px -20px rgba(255, 90, 48, 0.16),
      0px 26px 24px -10px rgba(255, 90, 48, 0.1),
      0px 12px 10px -6px rgba(255, 90, 48, 0.08),
      0px 4px 4px -4px rgba(30, 33, 44, 0.05);
    &::before {
      opacity: 1;
    }

    &::after {
      opacity: 1;
    }

    .work-services-item__icon {
      fill: var(--color-light);
    }

    .work-services-item__title {
      color: var(--color-light);
    }
  }
}
